import { BASE_URL } from './baseURL'
import { store } from '../index.js'

export const INITIAL_STATE = 'INITIAL_STATE';
export const CURRENT_CART = 'CURRENT_CART';
export const ADD_ITEM = 'ADD_ITEM';
export const REMOVE_ITEM = 'REMOVE_ITEM';
export const SUB_QUANTITY = 'SUB_QUANTITY';
export const ADD_QUANTITY = 'ADD_QUANTITY';
export const ADD_SHIPPING = 'ADD_SHIPPING';
export const SUB_SHIPPING = 'SUB_SHIPPING';
export const ADD_MSG = 'ADD_MSG'
export const ADD_PROMO = 'ADD_PROMO';
export const CREATE_ORDER = 'CREATE_ORDER';

export const initState = ()=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/product/initial`,{
            method: 'GET'
        }).then(res=> res.json())
        .then(data=> dispatch({ type: INITIAL_STATE,  items: data }))
    }
}

export const currentCart = ()=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/cart/current_cart`, {
            credentials: 'include', 
            method: 'GET'
        }).then(res=> res.json())
        .then(data=> dispatch({ type: CURRENT_CART, data }) )
    }
}
export const addItem = (id)=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/cart/item`, {
            headers: {'Content-Type':'application/json'},
            credentials: 'include', 
            method: 'POST',
            body: JSON.stringify({product_id: id, quantity: 1})
        }).then(res=> res.json())
        .then(data=> dispatch({ type: ADD_ITEM, data }))
    }
}
export const removeItem = ({cartID, product_id})=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/cart/item`, {
            headers: {'Content-Type':'application/json'},
            credentials: 'include', 
            method: 'DELETE',
            body: JSON.stringify({cartID, product_id})
        }).then(res=> res.json())
        .then(data=> dispatch({ type: REMOVE_ITEM, data }))
    }
}
export const subtractQuantity = ({cartID, product_id})=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/cart/sub`, {
            headers: {'Content-Type':'application/json'},
            credentials: 'include', 
            method: 'POST',
            body: JSON.stringify({cartID, product_id})
        }).then(res=> res.json())
        .then(data=> dispatch({ type: SUB_QUANTITY, data }))
    }
}
export const addQuantity = ({cartID, product_id})=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/cart/add`, {
            headers: {'Content-Type':'application/json'},
            credentials: 'include', 
            method: 'POST',
            body: JSON.stringify({cartID, product_id})
        }).then(res=> res.json())
        .then(data=> dispatch({ type: ADD_QUANTITY, data }))
    }
}
export const addShipping = (data)=>{
    return function(dispatch){
        dispatch({ type: ADD_SHIPPING, data })
    }
}
export const subShipping = (id)=>{
    return { type: SUB_SHIPPING, id }
}
export const addMsg = (data) => {
    return function(dispatch){
        dispatch({ type: ADD_MSG, data})
    }
}
export const addPromo = (params)=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/promo/validate`, {
            headers: {'Content-Type':'application/json'},
            credentials: 'include', 
            method: 'POST',
            body: JSON.stringify({promo: params})
        }).then(res=> res.json())
        .then(data=> dispatch({ type: ADD_PROMO, data }))
    }
}
export const addOrder = ()=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/order/create`, {
            headers: {'Content-Type':'application/json'},
            credentials: 'include', 
            method: 'POST',
            body: JSON.stringify({ cart: store.getState().cart, profile: store.getState().currentUser.profile})
        }).then(res=> res.json())
        .then(data=> dispatch({ type: CREATE_ORDER, data }))
    }
}

