import { Route, Redirect } from 'react-router-dom'

export default function AdminAuth({isAuth, component: Component, ...restProps}) {

    return(
        <Route {...restProps}
            render={
                (routeProps)=>{
                    if (isAuth.admin){
                        return <Component {...routeProps}/>
                    } else {
                        return <Redirect to='/' />
                    }
                }
            }
        />
    )
}