import './index.scss'
import { useSelector } from 'react-redux'

export default function Order({ order_id, email }) {
    
    const { order } = useSelector(state => ({
        order: state.order.order
    }))

    return (
        <div className='wrapper'>
			 <div className='title'>
                <span>Order Confirmation</span>
            </div>
            <div className='success'>
                <p>Your Order: </p>
                <h5>{order && order.orderID}</h5>
                {order && order.cart && order.cart.addedItems.map((o, key)=> (
                    <div key={key}>
                    <span>{o.product_id}</span>
                    <span>---</span>
                    <span>{o.quantity}</span>
                    </div>
                ))}
                <div>
                    <p>{order.profile.addressString}</p>
                    <p>{`${order.profile.first_name} ${order.profile.last_name}`}</p>
                    <p>{order.profile.phone}</p>
                </div>
            </div>
        </div>
    )
}