import { INITIAL_STATE, CURRENT_CART, ADD_ITEM, REMOVE_ITEM, SUB_QUANTITY, ADD_QUANTITY, ADD_SHIPPING, SUB_SHIPPING, ADD_MSG, ADD_PROMO, CREATE_ORDER } from '../actions/cartActions'

let initState = { addedItems: [], items: [], promo: {}, total: 0, subtotal: 0,  discount: 0, shipping: 0, msg:'', order:'' } 

export const cart = (state=initState, action)=>{
    if (action.type === INITIAL_STATE){
        return {  ...state, items: action.items }
    }
    if (action.type === CURRENT_CART){
        let newTotal = action.data.items ? action.data.items.reduce((acc,i)=>{ 
            return acc + i.quantity * state.items.find(p=>p.productID === i.product_id).price 
        }, 0) : 0
        return { ...state, addedItems: action.data.items, total: newTotal }
    }
    if (action.type === ADD_ITEM){
        let copyAddedItems = state.addedItems ? state.addedItems : new Array(0)
        let addedItem = state.items.find(p=> p.productID === action.data.product_id)
        let existed_item = state.addedItems ? state.addedItems.find(p=>p.product_id === action.data.product_id) : null
        if (existed_item){
            existed_item.quantity += 1 
            return { ...state, total: state.total + addedItem.price }
        } else {
            return { ...state, addedItems: [...copyAddedItems, action.data], total: state.total + addedItem.price }
        }
    }
    if(action.type === REMOVE_ITEM){
        let removeItem = state.items.find(p=> p.productID === action.data.product_id)
        let itemToRemove = state.addedItems.find(p=> p.product_id === action.data.product_id)
        let new_items = state.addedItems.filter(p=> p.product_id !== action.data.product_id)
        return { ...state, addedItems: new_items, total: state.total - (removeItem.price * itemToRemove.quantity) }
    }
    if (action.type === ADD_QUANTITY){
        let addedItem = state.items.find(p=> p.productID === action.data.product_id)
        state.addedItems.find(p=>p.product_id === action.data.product_id).quantity += 1
        return { ...state, total: state.total + addedItem.price }
    }
    if (action.type === SUB_QUANTITY){  
        let addedItem = state.items.find(p=> p.productID === action.data.product_id)
        let existed_item = state.addedItems.find(p=>p.product_id === action.data.product_id) 
        if (existed_item.quantity === 1){
            let new_items = state.addedItems.filter(p=>p.product_id !== action.data.product_id)
            return { ...state, addedItems: new_items, total: state.total - addedItem.price }
        } else {
            existed_item.quantity -= 1
            return { ...state, total: state.total - addedItem.price }
        }
    }
    if (action.type === ADD_SHIPPING){
        if (state.discount){
            return { ...state, shipping: action.data, subtotal: state.total + action.data - state.discount }
        } else {
            return  { ...state, shipping: action.data, subtotal: state.total + action.data }
        }
    }
    if (action.type === SUB_SHIPPING){
        return { ...state, total: state.total - 0 }
    } 
    if (action.type === ADD_MSG){
        return { ...state, msg: action.data}
    }
    if (action.type === ADD_PROMO){
        if (action.data.error) {
            return { ...state, discount: 0, subtotal: state.total + state.shipping }
        } else {
            let discount = state.total * (action.data.percent/100.00)
            if (state.shipping) {
                return { ...state, promo: action.data,  discount: discount, subtotal: state.total - discount + state.shipping }
            } else {
                return { ...state, promo: action.data,  discount: discount, subtotal: state.total - discount }
            }
        }
    } 
    if (action.type === CREATE_ORDER){
        return { ...state, order: action.data.orderID }
    }
    else {
        return state
    }
}
