import { BASE_URL } from './baseURL'

export const CURRENT_USER = 'CURRENT_USER';
export const CREATE_SESSION = 'CREATE_SESSION';
export const DESTROY_SESSION = 'DESTROY_SESSION';
export const CREATE_USER = 'CREATE_USER';
export const CREATE_PROFILE = 'CREATE_PROFILE';

export const currentUser = ()=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/auth/current_user`, {
            headers: { 'Content-Type': 'application/json' },
            credentials: 'include', 
            method: 'GET'
        }).then(res=>res.json())
        .then(data=> {
            if (data.email) dispatch({ type: CURRENT_USER, currentUser: data })
        })
        
    }
}
export const createSession = (params)=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/auth/session`, {
            headers: { 'Content-Type':'application/json' },
            credentials: 'include', 
            method: 'POST',
            body: JSON.stringify(params)
        }).then(res=>res.json())
        .then(data=> dispatch({ type: CREATE_SESSION, currentUser: data }))
    }
}
export const destroySession = ()=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/auth/session`, {
            credentials: 'include', 
            method: 'DELETE'
        }).then(res=>res.json())
        .then(()=> dispatch({ type: DESTROY_SESSION }))
    }
}
export const createUser = (params)=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/auth/create_user`, {
            headers: { 'Content-Type':'application/json' },
            credentials: 'include', 
            method: 'POST',
            body: JSON.stringify(params)
        }).then(res=>res.json())
        .then(data=> dispatch({ type: CREATE_USER, currentUser: data }))
    }
}
export const createProfile = (data)=>{
    return function(dispatch){
        // return fetch(`${BASE_URL}/auth/create_user`, {
        //     headers: { 'Content-Type':'application/json' },
        //     credentials: 'include', 
        //     method: 'POST',
        //     body: JSON.stringify(params)
        // }).then(res=>res.json())
        // .then(data=> dispatch({ type: CREATE_PROFILE, currentUser: data }))
        dispatch({ type: CREATE_PROFILE, data })
    }
}
