import './index.scss'
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';

export default function Maintenance() {
    const Home = ()=> (
        <div className='maintenance'>
            <img src={process.env.PUBLIC_URL + '/images/kavaglam_logo_long.svg'} alt='KAVAGLAM' />
            <ul>
                <li>we'll be back soon</li>
            </ul>
        </div>
    )
    
    return (
        <BrowserRouter>
			<Switch>
				<Route exact path='/' component={Home} />
                <Redirect from='*' to='/' />
                {/* <Route render={() => <Redirect to="/" />} /> */}
			</Switch>
		</BrowserRouter>
    )
}
