import './index.scss'
import { Link } from 'react-router-dom';
import { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { addShipping, subShipping } from '../../actions/cartActions'
import Summary from '../Summary'
import CanadaPostClient from 'canadapost-api'

export default function Shipping({ history }) {
	const cpClient = new CanadaPostClient(
		process.env.REACT_APP_CANADAPOSTCLIENT_USERID, 
		process.env.REACT_APP_CANADAPOSTCLIENT_PASSWORD, 
		[process.env.REACT_APP_CANADAPOSTCLIENT_CUSTOMER, ['en-CA', [true]]]
	);

	const dispatch = useDispatch()
	const { profile } = useSelector(state => ({
		profile: state.currentUser.profile
    }))
	const handleChecked = e => e.target.checked ?  dispatch(addShipping(0)) : dispatch(subShipping())
	const handleFormSubmit = e => {
        e.preventDefault()
        history.push("/payment")
    }

	useEffect(()=> {
		if (profile) {
			cpClient.discoverServices('V3M5X2', profile.address.countryShort, profile.address.postal_code.replace(/\s/g, ''))
				.then(data=> console.log(data))
		}
	}, [])
	
    return (
        <div className='wrapper'>
			 <div className='title'>
                <span>Shipping</span>
            </div>
			<div className="shipping">
				<div className="shipping-panel">
					<span>
						cart 
						<svg height="24px" viewBox="0 0 24 24" width="20px" fill="#ccc"><path d="M0 0h24v24H0V0z" fill="none"/>
							<path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6-6-6z"/>
						</svg> 
						information
						<svg height="24px" viewBox="0 0 24 24" width="20px" fill="#ccc"><path d="M0 0h24v24H0V0z" fill="none"/>
							<path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6-6-6z"/>
						</svg>
						<strong>shipping</strong>
						<svg height="24px" viewBox="0 0 24 24" width="20px" fill="#ccc"><path d="M0 0h24v24H0V0z" fill="none"/>
							<path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6-6-6z"/>
						</svg>
						payment
					</span>
					<div className="shipping-info">
                        <span>Contact</span>
                        <span>{profile ? profile.email : ''}</span>
                        <Link to='/info'>Change</Link>
                        <hr/>
                        <span>Ship to</span>
                        <span>{profile ? profile.addressString : ''}</span>
                        <Link to='/info'>Change</Link>
                    </div>
                    <div className="shipping-method">
                        <span>Shipping Method</span>
                        <ul>
                            <li className="shipping-items">
                                <input type="radio" id="standard" name="standard" value="Standard" onChange={handleChecked}/>
                                <span>Standard</span>
                                <span>Free</span>
                            </li>
                        </ul>
                    </div>
					<form onSubmit={handleFormSubmit} className="form-submit">
						<input type='submit' className='btn btn-success' value='Continue to payment' />
						<Link to='/info'>Return to information</Link>
					</form>
				</div>
				<Summary />
			</div>
		</div>
    )
}