import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import { rootReducer } from './reducers/rootReducer';
import thunk from 'redux-thunk';
import App from './App';
import './index.scss';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Elements } from "@stripe/react-stripe-js";
import { loadStripe } from '@stripe/stripe-js';
import { initialState } from './initialState'
import Maintenance from './components/Maintenance'

export const store = createStore(rootReducer, initialState, applyMiddleware(thunk))


if (new URLSearchParams(window.location.search).get('admin') === 'true') {
    const date = new Date()
    date.setTime(date.getTime()+(60*60*1000))
    document.cookie = `admin = true; expires = ${date.toGMTString()}`
} 
const admin = /admin=true/.test(document.cookie) ?  true : false

ReactDOM.render(
    <Provider store={store}>
        <Elements stripe={loadStripe(process.env.REACT_APP_PUBLISHABLE_KEY)}>
          {admin ? <App /> : process.env.REACT_APP_MAINTENANCE === 'true' ? <Maintenance /> : <App /> }
        </Elements>
    </Provider>,
    document.getElementById('root')
);


