import './index.scss'
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
// import { addItem } from '../../actions/cartActions'

export default function Products({ history }) {
    const items = useSelector(state=>state.cart.items)
    const dispatch = useDispatch()

    return (
        <div className="wrapper">
            <div className="title">
                <span>Beauty Brushes</span>
            </div>
            <div className="products">
                {items.map((item,i)=>
                <div className="card" key={i}>
                    <div className="card-image">
                        <Link to={`/products/` + item.productID.replace(/\s/g,'')}>
                            <img src={item.images[0]} alt={item.productID}/>
                        </Link>
                        <span className="card-title">{item.productID} ~ ${item.price}</span>
                    </div>
                    {/* <div className="card-content">
                        <button onClick={()=> { dispatch(addItem(item.productID)); history.push('/cart') } }>Add to Cart</button>
                    </div> */}
                </div> 
                )}
            </div>
        </div>
    )
}