import { BASE_URL } from './baseURL'

export const ORDER = 'ORDER';

export const orders = ()=>{
    return fetch(`${BASE_URL}/order/orders`, {
        headers: { 'Content-Type':'application/json' },
        credentials: 'include', 
        method: 'POST'
    }).then(res=>res.json())
}
export const tracking = (params)=>{
    return fetch(`${BASE_URL}/order/tracking`, {
        headers: { 'Content-Type':'application/json' },
        credentials: 'include', 
        method: 'POST',
        body: JSON.stringify(params)
    }).then(res=>res.json())
}
export const myOrder = (params)=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/order/my_order`, {
            headers: { 'Content-Type':'application/json' },
            credentials: 'include', 
            method: 'POST',
            body: JSON.stringify(params)
        }).then(res=>res.json())
        .then(data=> dispatch({ type: ORDER, data }) )
    }
}