import { BASE_URL } from './baseURL'

export const CREATE = 'CREATE';
export const DELETE = 'DELETE';
export const APPROVE = 'APPROVE'
export const REVIEWS = 'REVIEWS';

export const reviews = ()=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/review/reviews`, {
            headers: { 'Content-Type':'application/json' },
            credentials: 'include', 
            method: 'GET'
        }).then(res=>res.json())
        .then(data=> dispatch({ type: REVIEWS, data }) )
    }
}
export const createReview = (body)=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/review/create_review`, {
            headers: { 'Content-Type':'application/json' },
            credentials: 'include', 
            method: 'POST',
            body: JSON.stringify(body)
        }).then(res=>res.json())
        .then(data=> dispatch({ type: CREATE, data }) )
    }
}
export const deleteReview = (body)=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/review/delete_review`, {
            headers: { 'Content-Type':'application/json' },
            credentials: 'include', 
            method: 'DELETE',
            body: body
        }).then(res=>res.json())
        .then(data=> dispatch({ type: DELETE, data: body }) )
    }
}
export const approveReview = (body)=>{
    return function(dispatch){
        return fetch(`${BASE_URL}/review/approve_review`, {
            headers: { 'Content-Type':'application/json' },
            credentials: 'include', 
            method: 'POST',
            body: body
        }).then(res=>res.json())
        .then(data=> dispatch({ type: APPROVE, data: body }) )
    }
}
export const getSignedRequest = (file)=>{
    return fetch(`${BASE_URL}/review/sign-s3?file-name=${file.name}&file-type=${file.type}`, {
        headers: { 'Content-Type':'application/json' },
        credentials: 'include', 
        method: 'GET'
    }).then(res=>res.json())
    .then(data => {
         fetch(data.signedRequest, {
            headers: { 'Content-Type':'multipart/form-data'},
            method: 'PUT',
            body: file
        })
        return data.url
    })
}
