import { BASE_URL } from './baseURL'

export const SUBSCRIBE = 'SUBSCRIBE';
export const UNSUBSCRIBE = 'UNSUBSCRIBE';
export const MAILING_LIST = 'MAILING_LIST';

export const subscribe = (params)=>{
    return fetch(`${BASE_URL}/newsletter/subscribe`, {
        headers: { 'Content-Type':'application/json' },
        credentials: 'include', 
        method: 'POST',
        body: JSON.stringify({email: params})
    }).then(res=>res.json())
    .then(data=> data)
}
export const unsubscribe = (params)=>{
    return fetch(`${BASE_URL}/newsletter/unsubscribe`, {
        headers: { 'Content-Type':'application/json' },
        credentials: 'include', 
        method: 'POST',
        body: JSON.stringify({email: params})
    }).then(res=>res.json())
    .then(data=> data)
}
export const listNewsletter = ()=>{
    return fetch(`${BASE_URL}/newsletter/newsletter_list`, {
        headers: { 'Content-Type':'application/json' },
        credentials: 'include', 
        method: 'POST'
    }).then(res=>res.json())
    .then(data=> data)
}


  