import { CURRENT_USER, CREATE_SESSION, DESTROY_SESSION, CREATE_USER, CREATE_PROFILE } from '../actions/userActions'

const initState = { email: '', admin: false, profile: {}}
export const currentUser = (state=initState, action)=>{
    switch (action.type){
        case CURRENT_USER:
            return { ...state, email: action.currentUser.email, admin: action.currentUser.admin }
        case CREATE_SESSION:
            return { ...state, email: action.currentUser.email, admin: action.currentUser.admin }
        case DESTROY_SESSION:
            return initState
        case CREATE_USER: 
        return { ...state, email: action.currentUser.email }
        case CREATE_PROFILE: 
            return { ...state, profile: action.data }
        default: 
            return state
    }
}